:root {
  --ins-color--orange: #ec7a08; }

button:focus {
  outline: none; }

.ins-c-inventory__table--toolbar:not(.ins-c-inventory__table--toolbar-has-items) .pf-c-data-toolbar__content .pf-c-data-toolbar__item:last-child {
  min-width: 200px;
  min-width: 12.5rem; }

.ins-c-inventory__table--toolbar:not(.ins-c-inventory__table--toolbar-has-items) .pf-c-pagination__nav-page-select input {
  min-width: 75px;
  min-width: 4.6875rem; }

.ins-c-table__empty {
  margin: 75px;
  margin: 4.6875rem;
  border-bottom: none; }
  .ins-c-table__empty .pf-c-content {
    text-align: center; }

.ins-c-inventory__tags-filter ul label.pf-c-check__label {
  display: flex;
  width: 100%; }
  .ins-c-inventory__tags-filter ul label.pf-c-check__label .pf-c-badge {
    margin-left: auto; }

.ins-c-inventory__tags-filter ul .ins-c-inventory__tags-tail {
  display: flex; }
  .ins-c-inventory__tags-filter ul .ins-c-inventory__tags-tail .ins-c-inventory__tags-no-tags {
    margin: auto; }
  .ins-c-inventory__tags-filter ul .ins-c-inventory__tags-tail .pf-c-spinner {
    margin-left: auto; }

.ins-c-entity-table.pf-c-table .ins-composed-col div:nth-child(2) {
  color: var(--pf-global--link--Color);
  word-wrap: break-word;
  word-break: break-all;
  cursor: pointer; }
  .ins-c-entity-table.pf-c-table .ins-composed-col div:nth-child(2):hover {
    color: var(--pf-global--link--Color--hover); }

.ins-c-entity-table.pf-c-table .ins-health-status div {
  display: inline-block;
  padding-right: 20px; }
  .ins-c-entity-table.pf-c-table .ins-health-status div svg {
    position: relative;
    top: 2px;
    left: -2px; }

.ins-c-entity-table.pf-c-table .ins-composed-col :first-child {
  color: var(--pf-c-button--m-disabled--Color);
  font-size: var(--pf-global--FontSize--sm); }

.ins-c-entity-table.pf-c-table .ins-composed-col * {
  font-size: var(--pf-global--FontSize--lg); }

.ins-c-entity-table.pf-c-table .ins-composed-col .ins-composed-col {
  cursor: pointer; }

.ins-c-entity-table.pf-c-table .ins-c-inventory__list-tags {
  display: inline-block; }

.ins-c-entity-table.pf-c-table .ins-health-status div span:nth-child(2) {
  color: var(--pf-global--info-color--100);
  cursor: pointer; }

.ins-c-entity-table.pf-c-table .pf-c-table__action .pf-c-dropdown {
  position: relative;
  left: 10px;
  top: 3px; }

.ins-simple-table .ins-health-status div {
  display: inline-block;
  padding-right: 20px; }
  .ins-simple-table .ins-health-status div svg {
    position: relative;
    top: 2px;
    left: -2px; }
